.fadeStory {
    opacity: 0;
    pointer-events: none;
    transition: all 1.5s;
    pointer-events: none;
}

.nonFadeStory {
    visibility: visible;
    transition: all 0.5s;
}

.outContainer {
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.4);
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
}

.modal {
    width: 18rem;
    height: 23rem;
    background-color: rgba(0, 0, 0, 0.0);
    position: absolute;
    left: 50%;
    top: 35%;
    transform: translate(-50%, -50%);
    z-index: 100;
    border: 1px solid white;
}

.outDiv {
    width: 20rem;
    height: 21rem;
}

