.modal_title {
    text-align: center;
    color: aliceblue;
    font-size: 2.5rem;
}

.modal_close {
    background-image: url(../lib/icon_close.png);
    background-size: 100%;
    background-repeat: no-repeat;
    position: relative;
    left: 96%;
    top: -4%;
    width: 1.5rem;
    height: 2rem;
}


.slick-prev::before,
.slick-next::before {
    color: aliceblue;
}

.slick-dots li.slick-active button:before {
    opacity: .75;
    color: #8b3795;
}

