.outContainer_menu {
    width: 100vw;
    height: 2rem;
}

.menuButton {
    color: aliceblue;
    font-size: 1.3rem;
    line-height: 2rem;
    display: block;
    text-align: end;
    margin-right: 0.5rem;
    margin-left: 92%;
}

.menuItemDiv {
    float: right;
    width: 30vw;
    margin: auto;
    padding-left: 0.2rem;
    max-height: 0;
    overflow: hidden;
    opacity: 0;
    transition: all ease-in 0.5s;
    margin-right: 0.5rem;
}

.menuItemDivShow {
    float: right;
    width: 30vw;
    margin: auto;
    padding-left: 0.2rem;
    max-height: 12rem;
    overflow: visible;
    opacity: 1;
    transition: all ease-in 0.5s;
    margin-right: 0.5rem;
}

.menuItem {
    color: aliceblue;
    font-size: 0.7rem;
    text-align: right;
}

.menuItem:hover {
    color: aliceblue;
    font-weight: bold;
}

menuitem:active {
    color: aliceblue;
    font-weight: bold;
}