.MainInforFade {
    opacity: 0;
}

.MainInforNonFade {
    opacity: 1;
    transition: all ease 2.0s;
}

.MainInforNickname {
    position: absolute;
    font-size: 1rem;
    color: aliceblue;
    text-align: center;
    margin-top: 3rem;
    left: 50%;
    transform: translate(-50%, 0);
}

.MainInforOpenDate {
    position: absolute;
    font-size: 1rem;
    color: rgb(199 106 145);
    text-align: center;
    margin-top: 5rem;
    left: 50%;
    transform: translate(-50%, 0);
}

.modifyName {
    background-image: url(https://cdn-icons-png.flaticon.com/512/4906/4906388.png);
    background-size: contain;
    background-repeat: no-repeat;
    width: 1rem;
    height: 3vh;
    position: absolute;
}

.turnOn {
    background-image: url(https://cdn-icons-png.flaticon.com/512/4647/4647348.png);
    background-size: contain;
    background-repeat: no-repeat;
    width: 2rem;
    height: 3vh;
    position: absolute;
}