.outContainer_Modal_ModifyNickname {
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.1);
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
}

.modal_Modal_ModifyNickname {
    width: 18rem;
    height: 7rem;
    background-color: rgb(17, 15, 27);
    position: absolute;
    left: 50%;
    top: 21%;
    transform: translate(-50%, -50%);
    z-index: 100;
    border: 1px solid white;
}

.M_ModifyNickname_input {
    width: 17rem;
    height: 2rem;
    display: block;
    margin: auto;
}

.M_ModifyNickname_h5 {
    color: aliceblue;
    margin: auto;
    padding-top: 0.5rem;
    padding-left: 0.5rem;
    position: absolute;
}

.M_ModifyNickname_h5_2 {
    color: aliceblue;
    margin: auto;
    padding-top: 0.5rem;
    left: 84%;
    position: absolute;
}

.modal_close_Modal_ModifyNickname {
    background-image: url(../../lib/icon_close.png);
    background-size: 100%;
    background-repeat: no-repeat;
    position: relative;
    left: 96%;
    top: -11%;
    width: 1.5rem;
    height: 2rem;
}