.fade {
    opacity: 0;
    transition: all 1.5s;
    pointer-events: none;
}

.outContainer {
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.4);
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
}

.modalLogin {
    width: 18rem;
    height: 23rem;
    background-color: black;
    position: absolute;
    left: 50%;
    top: 35%;
    transform: translate(-50%, -50%);
    z-index: 100;
    border: 1px solid white;
}

.modal_title_Login {
    text-align: center;
    color: aliceblue;
    font-size: 2.5rem;
    margin-top: 20%;
    margin-bottom: 5%;
}

img {
    width: 13rem;
    position: relative;
    left: 14%;
    top: 5%;
}

p {
    color: #888a8d;
    font-size: 0.5rem;
    text-align: center;
    position: relative;
    top: 30%;
}

.modal_close {
    background-image: url(../lib/icon_close.png);
    background-size: 100%;
    background-repeat: no-repeat;
    position: relative;
    left: 96%;
    top: -4%;
    width: 1.5rem;
    height: 2rem;
}