.MainInforFade {
    opacity: 0;
}

.MainInforNonFade {
    opacity: 1;
    transition: all ease 2.0s;
}

.outContainerLetter {
    margin: auto;
    width: 85%;
    height: 40vh;
    margin-top: 15rem;
}

.outDivLetter {
    text-align: center;
    height: 30vh
}

.letter1 {
    border: none;
    background-color: transparent;
    background-image: url(https://cdn-icons-png.flaticon.com/512/2733/2733072.png);
    background-repeat: no-repeat;
    background-size: cover;
    width: 3rem;
    height: 3rem;
    margin-left: 0.5rem;
    margin-right: 0.5rem;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
}

